@import url(https://fonts.googleapis.com/css?family=Poppins:200,300, 500, 700i,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-item {
  font-weight: 300;
}

.visa-header-image {
  max-height: 45vh;
}

.header-text {
  font-weight: 300;
  font-size: 1.7rem;
}

.header-search {
  font-weight: 200;
}

/* Search Box Scrollbar Style*/
.suggestionBoxActive {
  height: 200px;
}

.suggestionBoxInActive {
  height: 0px;
}

.suggestionBox {
  overflow: auto;
  scrollbar-color: #6c58cc #f2edeb;
}

.suggestionBox > div {
  padding: 5px;
  font-weight: 300;
  font-size: 0.9rem;
  border-top: 1px solid #a6acec;
  background-color: white;
}

.suggestionBox > div:hover {
  background-color: #6c58cc;
  cursor: pointer;
}

/* Add Filter Button Style*/
.btn-secondary {
  background-color: #6b62fe;
}

.btn-secondary:focus,
.btn-secondary.focus {
  background-color: #6b62fe;
}

/* Add Filter Button Style*/
.FilterPanel {
  background-color: #fcfcfc;

  border-radius: 5px;
  font-size: 0.8rem;
}

.btn-outline-dark {
  background-color: #fff;
  border-color: #efefef;
  border-width: 2px;
}

.btn-outline-dark:hover {
  background-color: #a8a8a8;
  color: #fff;
  border-color: #efefef;
}

.btn-outline-dark:not(:disabled):not(.disabled):active,
.btn-outline-dark:not(:disabled):not(.disabled).active,
.show > .btn-outline-dark.dropdown-toggle {
  color: #fff;
  background-color: #a8a8a8;
  border-color: #fff;
}

.btn-outline-dark:not(:disabled):not(.disabled):active:focus,
.btn-outline-dark:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-dark.dropdown-toggle:focus {
  box-shadow: none;
}

.btn-outline-dark:focus,
.btn-outline-dark.focus {
  box-shadow: none;
}

@media (min-width: 768px) {
  .container-small {
    width: 500px;
  }
}
@media (min-width: 992px) {
  .container-small {
    width: 500px;
  }
}
@media (min-width: 1200px) {
  .container-small {
    width: 500px;
  }
}

.container-small {
  max-width: 100%;
}

.countryInfoBox {
  overflow: hidden;
  height: 250px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.countryImage {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-origin: border-box;
  border-radius: 5%;
}

.countryName {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  font-weight: 800;
  font-size: 1.7rem;
  text-align: center;
}

.visaStatus {
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -20%);
          transform: translate(-50%, -20%);
  background-color: #ff6584;
  font-weight: 800;
  font-size: 0.8rem;
  text-align: center;
  border-radius: 5%;
}

.container {
  /* border-style: dashed; */
}

.contactus {
  max-height: 90vh;
  overflow: hidden;
}
.div {
  border-color: brown;
  border-style: dotted;
}

.footer {
  background-color: #3f3d56;
  border-top: bisque;
}

body,
html {
  background-color: #aba6ff;
  font-family: "Poppins", sans-serif;
}

